// get post


// 
import {
	errdata
} from 'api/errdata.js'


// GETtoken
let listing = function(urling, shopdata) {
	return new Promise((resolve, reject) => {
		var setdata = uni.getStorageSync('usermen') //token
		shopdata.token = setdata
		uni.request({
				url: urling,
				method: 'GET',
				data: shopdata
			})
			.then((res) => {
				resolve(res[1])

			})
			.catch((err) => {
				let errs = ' '
				errdata.errlist(errs)
				reject(err)
			})
	})
}

