// POST,
let publicing = function(urling, shopdata) {
	return new Promise((resolve, reject) => {
		uni.request({
				url: urling,
				method: 'POST',
				data: shopdata,
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},

			})
			.then((res) => {
				resolve(res[1])
			})
			.catch((err) => {
				let errs = ' '
				errdata.errlist(errs)
				reject(err)
			})
	})
}

